<template>
    <div class="bh-card bh-card-lv{{level}}">
        <slot></slot>
    </div>
</template>

<script>
    /**
     * ,
     * @module BhCard
     *
     * @example
     *     <caption>html</caption>
     *     <bh-card :level='2'>
     *         <p></p>
     *     </bh-card>
     */
    export default {
        /**
         * @property {int} [level=1] (z-index),
         */
        props: {
            level: {
                default: 1
            }
        }
    };
</script>



